import { Component, OnInit } from '@angular/core';
import { FormBuilder,FormGroup,Validators } from '@angular/forms';
import  {ReactiveFormsModule} from '@angular/forms';
import {Router,ActivatedRoute}  from '@angular/router';
import {OperationService}  from '../operation.service';
@Component({
  selector: 'app-bankedit',
  templateUrl: './bankedit.component.html',
  styleUrls: ['./bankedit.component.scss']
})
export class BankeditComponent implements OnInit {
bank:FormGroup;
    x: any;
    model: any = {};
    submitted = false;
    id:any;
    title:string;
  constructor(private fb : FormBuilder,private active:ActivatedRoute,private operation:OperationService,private router:Router) { }

update(){
      
     //insert and update bank

      if(this.id==undefined){

        this.submitted = true;
        if (this.bank.invalid) {
           
            return;
        }else{
       this.operation.insertdata(this.bank.value,"bank_reg")
       this.router.navigate(['bank'])
            }

      }else{
         this.submitted = true;

        // stop here if form is invalid
        if (this.bank.invalid) {
           
            return;
        }else{
          this.operation.updatedata(this.bank.value,'bank_update');
          
          this.router.navigate(['bank']);
        }
        
        console.log(this.bank.invalid)
        alert('SUCCESS!! :-)')
   
        
      }
}


  ngOnInit() {
  	this.active.params.subscribe(params=>{
       this.id=params['id'];
       

         if(this.id==undefined){
              this.title="Add bank";
           }
           else{
             this.title="Update bank"
             this.operation.Edit(params['id'],"editbank").subscribe(res=>{
           this.x=res[0];
             // put values in the form
             console.log(res)
      this.bank.setValue({
      bank_id:this.x.bank_id,
      name:this.x.name,
      user_id:this.x.user_id,
      ifsc:this.x.ifsc,
      created_at:this.x.created_at,
      updated_at:this.x.updated_at
     

       


                });
         })
           }
  });



  	  	this.bank=this.fb.group(
  	{
  		
     
   
   bank_id:[''],
   name:['',Validators.required],
   user_id:['',Validators.required],
   ifsc:['',Validators.required],
   created_at:['',Validators.required],
   updated_at:['',Validators.required]

  	});
  }

   get f() { return this.bank.controls; }


  }


